@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&display=swap);
:root {
  /* PRIMARY FONTS */
  --fonts-montserrat: "Montserrat", sans-serif;

  /* DERIVED FONTS */
  --fonts-paragraph: var(--fonts-montserrat);
  --fonts-heading: var(--fonts-montserrat);
  --fonts-help-text: var(--fonts-montserrat);

  /* PRIMARY COLOR PALLET */
  --primary-bg: #ffffff;
  --primary-bg-always: #ffffff;
  --primary-bg-lighter: rgba(255, 255, 255, 0.9);
  --primary-bg-always-light: #ffffff;

  --primary-black-one: #455a64;
  --primary-purple: #543fd3;
  --primary-yellow: #efb76c;
  --primary-red: #e64b31;
  --primary-dark-green: #41868e;
  --primary-light-green-one: #c8dede;
  --primary-light-green-two: #a4c8c8;
  --primary-gray: #a8a8a8;
  --secondary-bg-one: #fafafa;
  --secondary-bg-two: #e5e5e5;
  --border_gray: #949494;
  --hint-or-error-message-color: #595959;
  --text-color-always-black: #1b1919;
  --dark-bg: #021529;
  --grey-border: #f8f8f8;

  /* INHERITED COLORS */
  --text-color-black: var(--text-color-always-black);
}

[data-theme="dark"] {
  /* https://dbramwell.github.io/react-animate-on-scroll/#home */
  --primary-bg: #161718;
  --primary-bg-always: #ffffff;
  --primary-bg-lighter: rgba(22, 23, 24, 0.9);
  --primary-bg-always-light: #ffffff;
  --primary-black-one: #455a64;
  --primary-purple: #543fd3;
  --primary-yellow: #efb76c;
  --primary-red: #e64b31;
  --primary-dark-green: #41868e;
  --primary-light-green-one: #c8dede;
  --primary-light-green-two: #a4c8c8;
  --primary-gray: #a8a8a8;
  --secondary-bg-one: #fafafa;
  --secondary-bg-two: #e5e5e5;
  --border_gray: #949494;
  --hint-or-error-message-color: #595959;
  --text-color-always-black: #c2bfa0;
  --dark-bg: #021529;

  --grey-border: #f8f8f8;

  /* INHERITED COLORS */
  --text-color-black: var(--text-color-always-black);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff !important;
  background-color: var(--primary-bg) !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* GLOBAL CSS */
.basic-button_one {
  background-color: #efb76c;
  background-color: var(--primary-yellow);
  color: #ffffff;
  color: var(--primary-bg);
  font-family: "Montserrat", sans-serif;
  font-family: var(--fonts-heading);
  font-size: 18px;
  font-weight: 900;
  border-radius: 40px;
  padding: 10px 26px;
  border: none;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.basic-button_one:hover {
  outline: none;
  background-color: #41868e;
  background-color: var(--primary-dark-green);

  text-decoration: none;
  color: #ffffff;
  color: var(--primary-bg);
}

.basic-button_one:focus {
  outline: none;
}

.no-underline {
  text-decoration: none;
  color: white;
}

/* INPUTS */
.basic-input {
  background-color: #ffffff;
  background-color: var(--primary-bg);
  color: #41868e;
  color: var(--primary-dark-green);
  font-family: "Montserrat", sans-serif;
  font-family: var(--fonts-paragraph);
  font-weight: 900;
  font-size: 16px;
  border-radius: 7px;
  border: 2px solid #41868e;
  border: 2px solid var(--primary-dark-green);
  margin: 3px 0;
  margin-bottom: 24px;
  padding: 7px 13px;
  width: min(420px, 90%);
  transition: box-shadow 0.3s ease-in-out;
  cursor: text;
  width: 100%;
}

.basic-input:focus {
  outline: none;
  box-shadow: 0px 0px 5px 2px #41868e;
  box-shadow: 0px 0px 5px 2px var(--primary-dark-green);
}


.basic-input::-webkit-scrollbar {
  width: 7px;
}

.basic-input::-webkit-scrollbar-track {
  background: #ffffff;
  background: var(--primary-bg);
  border-radius: 7px !important;
}

.basic-input::-webkit-scrollbar-thumb {
  background-color: #ffffff;
  background-color: var(--primary-bg);
  border-radius: 10px;
  margin-right: 5px;
  border: 2px solid #ffffff;
  border: 2px solid var(--primary-bg);
}

.basic-input:hover::-webkit-scrollbar-thumb {
  background-color: #41868e;
  background-color: var(--primary-dark-green);
}


.basic-textarea {
  min-height: 44px;
  height: 300px;
  max-height: 600px;
  overflow-y: scroll;
}
